import React from "react"
import * as Styled from "./styled"
import { Link } from "gatsby"
import { motion } from "framer-motion"

const linkParentAnimation = {
    initial: {},
    completed: {}
}

const linkChildAnimation = {
    initial: {
        x: 0
    },
    completed: {
        x: 8,
    }
}


export default (props) => {
    return (
        <Styled.Wrapper>
            <h5>
                <Link to={props.to}>
                    <motion.div animate="initial" whileHover="completed" initial="initial" variants={linkParentAnimation} >
                        {props.children}
                        <motion.svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 14" variants={linkChildAnimation}>
                            <path d="M15.515 12.6l1.414 1.4 7.07-7-7.07-7-1.414 1.4 4.654 4.607H0v1.98h20.174l-4.66 4.613z"/>
                        </motion.svg>

                    </motion.div>
                </Link>
            </h5>
        </Styled.Wrapper>
    )
}