import styled from "styled-components"

export const Wrapper = styled.div`

    a {
        display: inline-block;
        line-height: var(--line-height-tight);
        margin: -8px;
        padding: 8px;
        position: relative;

        &:hover svg {
            fill: ${props => props.theme.link.hover};
        }
    
        svg {
            display: inline;
            fill: ${props => props.theme.link.normal};
            margin-left: 8px;
            position: relative;
            top: 2px;
            width: 24px;    
        }

    }

`